	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Who's Boss In The EU?","date":"28 Feb 2002","slug":"/2002-02-28-whos-boss-in-the-eu/","tags":"Y2002"},"html":"<p>Heard on <a href=\"http://www.ndr4.de\">NDR4</a>: The EU Commission is suing the EU Council of Ministers for overturning a Commission decision. The decision (involving pork subsidies in Portugal) was rather minor, but was overturned for purely politcal reasons.<br />\nIt's interesting in light of the ministers overturning the budget warning for Germany, and recent comments by European politicians that the Commission has become too powerful and that more European decision-making should be made at the national and regional level.<br />\nIt's also interesting that I cannot find a single web link to this story.</p>"},"next":{"fields":{"title":"Unfriendly Veal","date":"28 Feb 2002","slug":"/2002-02-28-unfriendly-veal/"}},"previous":{"fields":{"title":"Clear Head","date":"01 Mar 2002","slug":"/2002-03-01-clear-head/"}}},{"node":{"fields":{"title":"Unfriendly Veal","date":"28 Feb 2002","slug":"/2002-02-28-unfriendly-veal/","tags":"Y2002"},"html":"<p>The tone is a bit hard for my taste, but James Lilek's <a href=\"http://www.lileks.com/screed/olivegarden.html\">Olive Garden</a> piece is a classic piece how Europeans view Americans, and get it all wrong. My favorite bit, though, was about the unfriendly veal.</p>"},"next":{"fields":{"title":"Blame It On Haider","date":"28 Feb 2002","slug":"/2002-02-28-blame-it-on-haider/"}},"previous":{"fields":{"title":"Who's Boss In The EU?","date":"28 Feb 2002","slug":"/2002-02-28-whos-boss-in-the-eu/"}}},{"node":{"fields":{"title":"Blame It On Haider","date":"28 Feb 2002","slug":"/2002-02-28-blame-it-on-haider/","tags":"Y2002"},"html":"<p>Spiegel: <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,184731,00.html\">Schröder sagt Prag-Reise ab</a> (Schröder cancels Prague visit). It all started with Haider's campaign in Austria to block Czech entry to the EU. In an interview in response, Czech Prime Minister Zeman said that the Sudetendeutschen (expelled from Czechoslovakia in 1945) were all traitors loyal to Hitler. In the end both he and Schröder have election campaigns and would just as soon avoid the expulsion issue (seeing as they each have opponents more hard line on the issue). So let's just call the whole thing off.<br />\n<b>Update:</b> BBC: <a href=\"http://news.bbc.co.uk/hi/english/world/europe/newsid_1846000/1846915.stm\">Schroeder snubs Prague</a>.</p>"},"next":{"fields":{"title":"","date":"26 Feb 2002","slug":"/2002-02-26-schroeder-woos-the-union-on-immigration/"}},"previous":{"fields":{"title":"Unfriendly Veal","date":"28 Feb 2002","slug":"/2002-02-28-unfriendly-veal/"}}}],"pathPrefix":"","first":false,"last":false,"index":920,"pageCount":1101}}

